@import url(~vue-content-placeholders/dist/vue-content-placeholders.css);
.column {
  min-width: 310px;
  width: 310px;
  height: 100%;
  margin-right: 5px;
  display: flex;
  flex-direction: column;
  scroll-snap-align: start; }
  .column button.margin-left-auto {
    margin-left: auto; }
  .column button.margin-right-auto {
    margin-right: auto; }
  .column header {
    position: relative;
    padding: 0px 17px 0px 12px;
    margin-top: 4px; }
    .column header .actions {
      display: flex; }
  .column .form /deep/ .vdp-datepicker {
    width: 100%;
    margin-bottom: 24px; }
    .column .form /deep/ .vdp-datepicker input {
      width: 100%;
      height: 100%;
      border: none;
      height: 48px;
      border-radius: 4px;
      padding: 5px 12px; }
    .column .form /deep/ .vdp-datepicker .vdp-datepicker__clear-button {
      position: absolute;
      font-size: 16px;
      top: 14px;
      right: 10px; }
  .column h2.error {
    text-align: center;
    padding: 5px; }
  .column .documents {
    flex: 1;
    padding: 4px 12px 12px 12px; }
    .column .documents .tombstone {
      padding: 12px;
      user-select: none; }
    .column .documents .documents-gap {
      padding: 12px;
      text-align: center; }

.curtain-enter {
  opacity: 0;
  transform: translateY(-30px); }

.curtain-enter-active {
  transition: opacity 0.5s, transform 0.5s; }

.curtain-enter-to {
  opacity: 1;
  transform: translateY(0px); }

@keyframes move {
  0% {
    background-position: 0 0; }
  100% {
    background-position: 28px 0; } }
